<template>
  <div class="crypto-loader">
    <LoaderInfinityDual v-if="loading" class="crypto-loader__svg" :color="color" />
    <Icon ref="icon" :name="crypto + '-circle'" width="32px" height="32px" class="crypto-loader__icon" />
  </div>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import { nextTick, onMounted, ref, watch } from 'vue'
import LoaderInfinityDual from '@/components/UIKit/Loader/LoaderInfinityDual2'

export default {
  name: 'CryptoLoader',
  components: { LoaderInfinityDual, Icon },
  props: {
    crypto: {
      type: String,
      default: ''
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const icon = ref()
    const color = ref('')

    onMounted(() => {
      getColor()
    })

    watch(() => props.crypto, () => {
      getColor()
    })

    const getColor = () => {
      nextTick(() => {
        const path = icon.value.$el.getElementsByTagName('path')[0]
        color.value = path.getAttribute('fill')
      })
    }

    return {
      icon,
      color
    }
  }
}
</script>

<style lang="scss">
.crypto-loader {
  position: relative;

  &__svg {
    position: absolute;
    top: -2px;
    left: -2px;
  }
  &__icon {

  }
}
</style>
