<template>
<section class="coin-form">
  <h6 class="coin-form__title">
    {{ t('common.enterAmount') }}
  </h6>

  <div class="coin-form__body">
    <Input
      v-model="sendCoinAmount"
      class="coin-form__body__input"
      type="number"
      :label="t('homepage.currencyToSend', { currency: sendCoin?.title })"
    >
      <template #append>
        <CryptoLoader :crypto="sendCoin?.key" :loading="loadingCoins === 'sent'" />
      </template>
    </Input>

    <Input
      v-model="receiveCoinAmount"
      class="coin-form__body__input"
      type="number"
      :label="t('homepage.currencyToReceive', { currency: receiveCoin?.title })"
    >
      <template #append>
        <CryptoLoader :crypto="receiveCoin?.key" :loading="loadingCoins === 'receive'" />
      </template>
    </Input>

    <div class="coin-form__body__info">
      <div class="coin-form__body__info__item">
        <Icon
          class="coin-form__body__info__item-icon"
          name="coins"
        />
        <div class="coin-form__body__info__item-label">
          0.002 BTC
        </div>
      </div>
      <div class="coin-form__body__info__item">
        <Tooltip
          :text="t('coinForm.clockTooltip')"
        >
          <Icon
            class="coin-form__body__info__item-icon"
            name="clock"
          />
        </Tooltip>
        <div class="coin-form__body__info__item-label">
          {{ t('common.minute', 10) }}
        </div>
      </div>
    </div>
  </div>

  <Button
    class="coin-form__action"
    type="primary"
    @click="$emit('submit')"
  >
    {{ t('common.continue') }}
  </Button>
</section>
</template>

<script>
import { MODULES_NAMES } from '@/common/modules'
import { useGetters, useMutations } from '@/plugins/vuex'
import { computed, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import Button from '@/components/UIKit/Button'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import Tooltip from '@/components/UIKit/Tooltip'
import CryptoLoader from '@/components/UIKit/Loader/CryptoLoader'

export default {
  name: 'CoinForm',
  components: {
    CryptoLoader,
    Button,
    Input,
    Icon,
    Tooltip
  },
  setup () {
    const { t } = useI18n()
    const {
      getReceiveCoin: receiveCoin,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)
    const {
      SET_SEND_RECEIVE_AMOUNT,
      SET_SEND_COIN_AMOUNT
    } = useMutations(MODULES_NAMES.EXCHANGE)

    const loadingCoins = ref('')
    const timer = ref()

    const receiveCoinAmount = computed({
      get: () => receiveCoin.value.amount,
      set: val => {
        fakeLoading('sent', val)
        SET_SEND_RECEIVE_AMOUNT(val)
      }
    })
    const sendCoinAmount = computed({
      get: () => sendCoin.value.amount,
      set: val => {
        fakeLoading('receive', val)
        SET_SEND_COIN_AMOUNT(val)
      }
    })

    const fakeLoading = (type, val) => {
      if (timer.value) {
        loadingCoins.value = type
        clearTimeout(timer.value)
        timer.value = null
      }
      if (val) {
        loadingCoins.value = type
        timer.value = setTimeout(() => {
          // loadingCoins[type] = false
        }, 1200)
      } else {
        loadingCoins.value = type
      }
    }

    return {
      t,
      receiveCoinAmount,
      sendCoinAmount,
      loadingCoins,
      receiveCoin,
      sendCoin
    }
  }
}
</script>

